.App {
  text-align: center;
}

body{
 background-color: aqua;
 scroll-behavior: smooth;
 cursor: url(http://cur.cursors-4u.net/cursors/cur-11/cur1054.cur), auto !important;;
}
html{
  scroll-behavior: smooth;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#gotopbtn{
  position: fixed;
  width: 50px;
  height: 50px;
  background: #27ae60;
  bottom: 40px;
  right: 40px;
  text-decoration: none;
  text-align: center;
  line-height: 50px;
  color: white;
  font-size: 26px;
  border-radius: 50%;
  z-index: 5;
  display: none;
}

.gtbh{
  display: none;
  position: fixed;
  bottom: -25px;
  right: 1px;
  text-decoration: none;
  text-align: center;
  line-height: 50px;
  color: white;
  color: rgb(255, 47, 47);
  font-family: Lucida Bright;
  border-radius: 50%;
  z-index: 5;
}

#gotopbtn:hover+.gtbh{
       display: block;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
