.title{
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    width: 95%;
    text-align: center;
    font-size: 18px;
    color: aliceblue;
    z-index: 1;
  }

  .bg{
    background-position: 0% 0% ;
    width: 100vw;
    height: 100vh;
    background-color: rgba(3, 3, 3, 0.637);
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .bg0{
    width: 100vw;
    height: 100vh;
    z-index: -1;
    opacity: 1;
  }
  
  #bg1
    {
      margin: 0;
      position: absolute;
      top: 20%;
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      left: 0;
      right: 0;
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 50vw;
      height: 70vh;
      opacity: 0.2;   
      position: fixed;  
      animation: b1 9s ease-in-out infinite;
    }
    @keyframes b1{
      0%{
           transform: scaleX(1);
      }
      50%{
        transform: scaleX(-0.5);
       
      }

      100%{
        transform: scaleX(-1);
       
      }
     
}
  
    

    .bg2{
      margin: 0;
      position: absolute;
      top: 60%;
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      left: 0;
      right: 0;
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 50vw;
      animation: b2 12s linear infinite;
      z-index: 1;
    }

    @keyframes b2{
      0%{
           transform: translateX(-25vw);
      }
     50%{
      transform: translateX(25vw);
     }
      100%{
        transform: translateX(-25vw);
      }
     
}



    .cntr0 {
      position: absolute;
      top: 55%;
      left: 50%;
      transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      font-size:5.1vw;
      color: aquamarine;
      z-index: 1;
      animation: b30 10s ease 1;
      cursor: url(http://cur.cursors-4u.net/others/oth-1/oth56.cur), auto !important;  
      }

      @keyframes b30{
        0%   {color: red; }
       
        25%  {color: yellow;}
        50%  {color: rgb(0, 255, 55);}
        100% {color: aquamarine;}
      }

      .cntr1 {
        animation: b30 5s ease infinite;
        }

        @keyframes b30{
          0%   {color: red; }
         
          25%  {color: yellow;}
          50%  {color: rgb(0, 255, 55);}
          100% {color: aquamarine;}
        }
    
    
    
    .overlay0 {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      display:none;
      transition: 3s ease;
      
    }
    
    .cntr0:hover +.overlay0 {
      display: block;
    }
   
    
    .text0{
      background-image: linear-gradient(to left,aquamarine, violet,coral,aquamarine);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      z-index: 1;
      font-size: 3vw;
      position: absolute;
      top: 35%;
      left: 50%;
      transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
    }
