    .text10{
      font-size: 4vw;
      position: absolute;
      top: 70%;
      color: salmon;
      left: 20vw;
      animation: t10 12s linear infinite;
      opacity: 0;
     }
     .text11{
       font-size: 4vw;
      position: absolute;
      top: 70%;
      color: seashell;
      left: 45vw;
      animation: t10 12s linear infinite;
      opacity: 0;
     }
     .text12{
       font-size: 4vw;
       position: absolute;
      top: 70%;
      color: springgreen;
      left: 70vw;
      animation: t10 12s linear infinite;
      opacity: 0;

     }

     @keyframes t10{
       0%{
         opacity: 0;
       }
       50%{
        opacity: 1;
       }
      100%{
         opacity: 0;
       }
     }


    
   
  
   
 @keyframes b3{
      from{
           transform: translateY(-100rem);
      }
      to{
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
      }
     
}
 
